<template>
    <div class="create-message">
            <!-- <div class="vx-row mb-2">
                <div class="vx-col w-full">
                    <div class="flex">
                        <div class="flex-auto">
                            <label>Number WhatsApp</label>
                        </div>
                        <div class="flex-none">
                            <vs-button @click="$router.push('/whatsapp/use-number')" color="#0fabbc" size="small" icon="contact_phone"></vs-button>
                        </div>
                    </div>
                    <vs-textarea rows="2" class="w-full mt-2" v-model="formMessage.nomor"></vs-textarea>
                </div>
            </div> -->
            <div class="vx-row mb-2">
                <div class="vx-col w-full">
                    <label>Pesan</label>
                    <vs-textarea cols="30" rows="5" class="w-full mt-2" v-model="formMessage.narasi"></vs-textarea>
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                    <label>Tanggal Request</label>
                    <datepicker clas="w-full mt-2" :placeholder="'-- Tanggal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="formMessage.tglKirim" ></datepicker>
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                    <label>Tanggal Kirim</label>
                    <datepicker clas="w-full mt-2" :placeholder="'-- Tanggal --'" :minimumView="'day'" :maximumView="'month'" :format="'dd MMM yyyy'" v-model="formMessage.tglKirim" ></datepicker>
                </div>
            </div>
            <div class="vx-row mb-6">
                <div class="vx-col w-full">
                    <label>Gambar</label>
                    <img class="img-default img-view my-2" :src="formMessage.img">
                    <vs-input type="file" class="w-full mt-2" @change="handleImage" accept="image/*"  />
                </div>
            </div>
             <div class="vx-row mb-10">
                <div class="vx-col w-full">
                    <label>Target</label>
                    <v-select class="w-full mt-2" :placeholder="'- Pilih -'"  ></v-select>
                </div>
            </div>
            <div class="vx-row">
                <div class="vx-col w-full">
                    <vs-button class="float-right" color="success" @click="sendMessage">KIRIM</vs-button>
                    <!-- <vs-button class="float-right mr-3" color="grey" @click="addMessage = false">Batal</vs-button> -->
                </div>
            </div>
    </div>
</template>

<script>
import Datepicker from 'vuejs-datepicker'

export default {
    data () {
        return {
            file: '',
            formMessage: {
                nomor: '',
                tglKirim: '',
                narasi: '',
                img: null
            },
        }
    },
    components: {Datepicker},

    methods: {
        handleImage(e){
            const selectedImage = e.target.files[0];
            this.createBase64Image(selectedImage);
        },

        createBase64Image(fileObject){
            const reader = new FileReader();

            reader.onload = (e) => {
                this.formMessage.img = e.target.result;
            };
            reader.readAsDataURL(fileObject);
        },

        sendMessage() {
            let parmData = {
                nomor: this.formMessage.nomor,
                tglKirim: this.formMessage.tglKirim,
                img: this.formMessage.img,
                narasi: this.formMessage.narasi
            }
            this.$http.post( '/', parmData)
            .then(function(){
            // console.log('SUCCESS!!');
            })
            .catch(error => {
                this.$swal.fire(
                    'Oopss !',
                     error,
                    'error'
                )
            })
        }
    }
}
</script>

<style scoped>
    .img-view {
        width :70px;
        border-radius: 8px;
    }

    .img-default {
        width :200px;
        border-radius: 8px;
    }
</style>

